.boletos{
    padding: $separacion 0;
    
    h3{
        text-align: center;
        color: $blanco;
    }

    .contenedor-boletos{
        margin-top: 2.5rem;
        @include grid(2, $separacion)
    }

    .plan{
        p{
            color: $blanco;
            font-size: 3rem;
            text-align: center;
            margin-bottom: 2rem;
        }
    }
}

.pase{
    width: 100%;
    border-spacing: 0;
    thead{
        background-color: $rosa;

        th{
            padding: 1.25rem;
            color: $blanco;
            text-transform: uppercase;
            font-weight: $delgada;
            font-size: 2rem;
        }
    }
    tbody{
        background-color: $blanco;
        td{
            padding: 1.25rem;
            border-bottom: 1px solid #e1e1e1;
        }

        tr{
            &:last-of-type{ //Selecciona el ultimo
                td{
                    border: none;
                }
            }
        }

    }
    tfoot{
        background-color: $morado;
        td{
            padding: 1.25rem;
            text-align: center;
            font-size: 3rem;
            color: $blanco;
        }
    }
}
